<template>
    <div class="board h-100" ref="board">
        <div v-for="board_row in 2" :key="board_row" class="h-25 row board-row d-flex align-items-center">
            <div v-for="row_hole in 4" :key="row_hole" class="p-1" :style="{'width': hole_size, 'height': hole_size}">
                <div class="hole h-100 w-100">

                </div>
            </div>
        </div>

        <div v-for="board_row in 2" :key="board_row" class="h-25 row board-row d-flex align-items-center">
            <div v-for="row_hole in 4" :key="row_hole" class="p-1" :style="{'width': hole_size, 'height': hole_size}">
                <div class="hole h-100 w-100">

                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    data () {
        return {
            hole_size: '0px',
        }
    },
    methods: {
        update_hole_size() {
            let length = this.$refs.board.clientHeight
            let width = this.$refs.board.clientWidth
            let size = 0
            if (length < width) {
                size = length / 4
            }
            else {
                size = width / 4
            }
            this.hole_size = size + 'px'
        }
    },
    mounted() {
        this.update_hole_size()
        window.addEventListener("resize", this.update_hole_size)
    },
    beforeUnmount() {
        window.removeEventListener("resize", this.update_hole_size);
    }
}
</script>

<style scoped>
.board {
    border : 3px solid black;
    border-radius: 50px;
}
.hole {
    border-radius: 100%;
    border: 2px solid black;
}

.board-row {
    display: flex;
    justify-content: center;
}
</style>